body {
  background-color: #282c34;
  color: #FFF;
}

.App {
  text-align: center;
}

.eq-generator {
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.equation {
  font-size: calc(50px + 2vmin);
  padding: 10px;
}

.user-answer-input {
  width: 25%;
  font-size: calc(50px + 2vmin);
}

.user-answer {
  font-size: 20px;
}

ul {
  list-style-type: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
}

ul li a {
  color: #000;
}

ul li a {
  color: rgb(106, 105, 105);
}

ul li a.active {
  color: rgb(106, 105, 105);
}

a.visited {
  color: rgb(106, 105, 105);
}

.navigation {
  min-height: 50px;
  /* background-color: lightblue; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(164, 164, 164);
}

.home {
  background-color: lightpink;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(164, 164, 164);
}

.points {
  position: absolute;
  top: 120px;
  right: 25px;
  text-align: right;
}

.btn {
  background-color: #008CBA;
  border: none;
  color: black;
  padding: 15px 32px;
  margin: 10px;
  text-align: center;
  font-size: 20px;
}

.select-operator {
  border: none;
}

.login {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-message {
  width: 400px;
  margin: auto;
}